<template>
  <div id="fix">
    <div class="signInWarp" v-show="signShow">
      <div class="upBgWarp">
        <div class="signText">{{ signTitle }}-修改密码</div>
      </div>
      <div class="signIn">
        <div class="inputWarp">
          <el-input placeholder="请输入账号" v-model="input1" clearable class="itemInput">
            <template slot="prepend">账号</template>
          </el-input>
          <i v-if="input1E" class="el-icon-close inputState inputStateE"></i>
          <i v-if="input1S" class="el-icon-check inputState inputStateS"></i>
        </div>
        <div class="inputWarp">
          <el-input type="password" placeholder="请输入旧密码" v-model="input2" clearable class="itemInput">
            <template slot="prepend">旧密码</template>
          </el-input>
          <i v-if="input2E" class="el-icon-close inputState inputStateE"></i>
          <i v-if="input2S" class="el-icon-check inputState inputStateS"></i>
        </div>
        <div class="inputWarp">
          <el-input type="password" placeholder="请输入新密码" v-model="input3" clearable class="itemInput">
            <template slot="prepend">新密码</template>
          </el-input>
          <i v-if="input3E" class="el-icon-close inputState inputStateE"></i>
          <i v-if="input3S" class="el-icon-check inputState inputStateS"></i>
        </div>
        <div class="inputWarp">
          <el-input type="password" placeholder="请再输入新密码" v-model="input4" clearable class="itemInput">
            <template slot="prepend">确认</template>
          </el-input>
          <i v-if="input4E" class="el-icon-close inputState inputStateE"></i>
          <i v-if="input4S" class="el-icon-check inputState inputStateS"></i>
        </div>
        <el-button class="signBtn" type="primary" @click="signClickFn" :disabled="openBtn">提交</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { baseUrl } from '@/api/unsplash/baseUrl'
import lodash from 'lodash'
import supportWord from '@/supportWord/index'
export default {
  name: 'Sign',
  data () {
    return {
      signTitle: supportWord.title,
      signShow: true,
      input1: '',
      input1E: false,
      input1S: false,
      input2: '',
      input2E: false,
      input2S: false,
      input3: '',
      input3E: false,
      input3S: false,
      input4: '',
      input4E: false,
      input4S: false,
      imgSrc: '',
      code_id: '',
      baseUrl: baseUrl
    }
  },
  computed: {
    openBtn: function () {
      return !(this.input1 !== '' && this.input2 !== '' && this.input3 !== '' && this.input3 === this.input4)
    }
  },
  watch: {
    input3: function (val) {
      if (this.input3 !== '' && this.input3 === this.input4) {
        this.input4S = true
        this.input4E = false
        return true
      } else if (this.input4 !== '' && this.input3 !== this.input4) {
        this.input4S = false
        this.input4E = true
      } else {
        this.input4S = false
        this.input4E = false
      }
    },
    input4: function (val) {
      if (this.input3 !== '' && this.input3 === this.input4) {
        this.input4S = true
        this.input4E = false
        return true
      } else if (this.input4 !== '' && this.input3 !== this.input4) {
        this.input4S = false
        this.input4E = true
      } else {
        this.input4S = false
        this.input4E = false
      }
    }
  },
  created: function () {
  },
  methods: {
    signClickFn: lodash.debounce(function () { // click to signIn
      this.signInfn()
    }, 500),
    async signInfn () {
      try {
        let params = {
          login_name: this.input1,
          oldpwd: this.input2,
          newpwd: this.input3
        }
        let res = await this.$api.unsplash.fix(params)
        if (res.errno === '0') {
          this.$router.push({path: '/'})
          if (res.data) {
            // this.$router.push({path: '/reference/theme'})
          }
        } else if (res.errno === '4106') {
          this.input2E = true
          this.input2S = false
          let self = this
          setTimeout(function () {
            self.input2E = false
            self.input2S = false
          }, 3000)
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async identifyfn () {
      try {
        let params = {
          'captcha': this.input3,
          'code_id': this.code_id
        }
        let res = await this.$api.unsplash.identify(params)
        console.log(res)
        if (res.msg === '通过') {

        } else {
          this.notifyFn('提示', res.msg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    notifyFn (title, msg) {
      this.$notify({
        title: title,
        message: msg,
        type: 'warning',
        dangerouslyUseHTMLString: true,
        duration: 5000,
        position: 'top-right'
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  #fix{
    .signInWarp{
      min-width: 100vw;
      min-height: 100vh;
      position: absolute;
      top: 0;left: 0;
      z-index: 100;
      background: #b7bcbf;
      .upBgWarp{
        width: 100vw;
        height: 100vh;
        position: absolute;
        top: 0;left: 0;
        background: url(../../assets/signBg1.png) top left no-repeat;
        background-size: 100% 100%;
        .signText{
          height:60px;
          position: absolute;
          top: 30%;left: 50%;
          transform: translate(-50%, -50%);
          line-height:60px;
          font-size: 26px;
          font-weight: bold;
          letter-spacing: 6px;
          color: #ffffff;
          margin-bottom: 15px;
          text-shadow: 1px 1px 2px #000000;
        }
      }
      .signIn{
        width: 340px;
        /*height: 540px;*/
        padding: 0 40px;
        /*background: #c5c8ce;*/
        position: absolute;
        top: 55%;left: 70%;
        transform: translate(-50%, -50%);
        overflow: hidden;
        /*<!--&::before{-->*/
          /*<!--content: '';-->*/
          /*<!--display: inline-block;-->*/
          /*<!--width: 260%;-->*/
          /*<!--height: 150%;-->*/
          /*<!--border-radius: 50%;-->*/
          /*<!--background: #ffffff;-->*/
          /*<!--position: absolute;-->*/
          /*<!--top: 50%;left: 50%;-->*/
          /*<!--transform: translate(-50%, 14%);-->*/
        /*<!--}-->*/
        .itemInput{
        }
      }
      .inputWarp{
        position: relative;
        margin-bottom: 10px;
        .el-input-group__prepend {
          width: 85px;
        }
        .inputState{
          position: absolute;
          top: 50%;
          right: 0;
          transform: translate(120%, -50%);
          width: 22px;
          height: 22px;
          padding: 3px;
          border-radius: 50%;
          background: #ffffff;
        }
        .inputStateE{
          color: red;
        }
        .inputStateS{
          color: green;
        }
      }
      .signBtn{
        width: 260px;
      }
      .el-button--primary.is-disabled, .el-button--primary.is-disabled:active, .el-button--primary.is-disabled:focus, .el-button--primary.is-disabled:hover {
        color: #FFF;
        background-color: #409EFF!important;
        border-color: #409EFF!important;
      }
    }
  }
</style>



